::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}
:-webkit-scrollbar-track {
  background-color: #0000;
}

.hero {
  margin-top: 40px;
  position: relative;
}
.container__home {
  display: flex;
  flex-direction: column;
}

.category-list {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 228px;
  gap: 24px;
}
.book-category-container {
  margin-block: 40px;
}
.category-item {
  letter-spacing: -0.02em;
  color: #1119;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  pointer-events: all;
  cursor: pointer;
}
.js-current-category {
  letter-spacing: -0.02em;
  text-align: left;
  color: #4f2ee8;
  text-transform: uppercase;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}
.js-book-category {
  color: #b4afaf;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
  letter-spacing: 0.42px;
  text-transform: uppercase;
}
.books-colortitle {
  letter-spacing: -0.04em;
  text-align: left;
  color: #4f2ee8;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}
.books__image {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.books__overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  width: calc(100% + 1px);
  background: #eac645;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  position: absolute;
}
.books__overlay-text {
  text-align: center;
  fill: #111;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
}
.books__wrapper:hover .books__overlay {
  transform: translateY(-99%);
}
.books__wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}

.books-title {
  margin-block: 40px;
  letter-spacing: -0.04em;
  color: #111;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}
.books-colortitle {
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px; /* 108.333% */
  letter-spacing: -1.92px;
}
.books__info-title {
  line-break: auto;
  line-clamp: 180px;
  letter-spacing: -0.04em;
  text-align: left;
  color: #111;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  overflow: hidden;
}
.books__info-author {
  letter-spacing: -0.04em;
  text-align: left;
  color: #b4afaf;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.supporters {
  width: 100%;
  max-width: 358px;
  height: 358px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  flex-direction: column;
  padding: 24px 40px 20px;
  display: flex;
  margin-top: 40px;
  box-sizing: border-box;
}

.supporters-title {
  gap: 12px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}
.supporters-title h3 {
  letter-spacing: -0.04em;
  color: #fff;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
}
.supporters-button {
  cursor: pointer;
  width: 32px;
  min-height: 32px;
  background-color: #fff;
  border: none;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0;
  transition: transform 0.5s ease-in-out;
  display: flex;
  transform: rotateX(0);
}
.supporters__btn--up {
  transform: rotateX(180deg);
}

.swiper .supporters__item {
  display: flex;
  gap: 16px;
  align-items: center;
}
.swiper .supporters__img {
  object-fit: contain;
  object-position: left;
  height: 32px;
  width: 100%;
  filter: brightness(0) invert();

  aspect-ratio: 159 / 32;
}
.swiper .supporters__number {
  font-style: normal;
  font-weight: 400;
  color: white;
}

.see-more {
  width: 124px;
  height: 46px;
  border: 2px solid var(--accent-color);
  background-color: var(--main-bg-color);
  line-break: auto;
  letter-spacing: -0.01em;
  color: var(--main-text-color-light-theme);
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
  margin-left: auto;
  margin-right: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}
.see-more:hover,
.see-more:focus {
  background-color: #4f2ee8;
  color: white;
}
.all-categories__btn {
  letter-spacing: -0.01em;
  height: 50px;
  width: 200px;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin-top: 32px;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  display: block;
  cursor: pointer;
}
.all-categories__btn:hover {
  background-color: #4f2ee8;
  color: white;
}

.books-list {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.go-to-top {
  width: 54px;
  height: 54px;
  background-color: #4f2ee8;
  box-shadow: 0 0 10px 3px #4f2ee84d;
  border-radius: 50%;
  z-index: 8;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 70px;
  right: 20px;
  transition: all 500ms ease-in-out;
}
.go-to-top:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 10px #4f2ee84d;
}
.svg-go-to-top {
  stroke: #fff;
  width: 24px;
  height: 24px;
}
@media screen and (min-width: 768px) {
  .hero__grid {
    display: flex;
    flex-direction: row;
    gap: 40px;
  }
  .books-title {
    margin-top: 100px;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 52px; /* 108.333% */
    letter-spacing: -1.92px;
  }
  .category-list {
    height: 472px;
  }
  .books-list {
    display: grid;
    grid-template-columns: repeat(3, 218px);
    column-gap: 24px;
    row-gap: 40px;
  }
  .books__image {
    width: 218px;
    height: 316px;
  }

  .supporters {
    height: 100%;
    max-height: 472px;
    width: 100%;
    margin: 0;
  }

  .see-more {
    margin-top: 20px;
  }
}
@media screen and (min-width: 1280px) {
  .books-title {
    margin-top: 0;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 52px; /* 108.333% */
    letter-spacing: -1.92px;
  }
  .container__home {
    display: grid;
    column-gap: 40px;
    grid-template-columns: 358px 1fr;
  }
  .books-container {
    margin: 0;
  }
  .books__image {
    width: 190px;
    height: 270px;
  }

  .hero__grid {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  .category-list {
    height: 472px;
  }
  .books-list {
    display: grid;
    grid-template-columns: repeat(4, 190px);
  }
  .see-more {
    margin-top: 32px;
  }
}
@media screen and (min-width: 1440px) {
  .books-list {
    grid-template-columns: repeat(5, 180px);
  }
  .books__image {
    width: 180px;
    height: 256px;
  }
}
