@import "https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100;0,9..40,200;0,9..40,300;0,9..40,400;0,9..40,500;0,9..40,600;0,9..40,700;0,9..40,800;0,9..40,900;0,9..40,1000;1,9..40,100;1,9..40,200;1,9..40,300;1,9..40,400;1,9..40,500;1,9..40,600;1,9..40,700;1,9..40,800;1,9..40,900;1,9..40,1000&display=swap";
/*! modern-normalize v2.0.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-text-color-light-theme: #111;
  --main-text-color-dark-theme: #fff;
  --secondary-text-color: #b4afaf;
  --accent-color: #4f2ee8;
  --secondary-accent-color: #eac645;
  --main-bg-color: #f6f6f6;
  --main-bg-color-dark-theme: #202024;
  --timing-function: .25s cubic-bezier(.4, 0, .2, 1);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

body {
  font-family: DM Sans, sans-serif;
}

svg {
  pointer-events: none;
}

button, a {
  cursor: pointer;
}

.user-icon-bg {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  display: flex;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    width: 100%;
    max-width: 1280px;
    padding: 0 24px;
  }
}

@media screen and (width >= 1440px) {
  .container {
    max-width: 1440px;
  }
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}

:-webkit-scrollbar-track {
  background-color: #0000;
}

.hero {
  margin-top: 40px;
  position: relative;
}

.container__home {
  flex-direction: column;
  display: flex;
}

.category-list {
  flex-direction: column;
  gap: 24px;
  height: 228px;
  display: flex;
  overflow: hidden scroll;
}

.book-category-container {
  margin-block: 40px;
}

.category-item {
  letter-spacing: -.02em;
  color: #1119;
  pointer-events: all;
  cursor: pointer;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.js-current-category {
  letter-spacing: -.02em;
  text-align: left;
  color: #4f2ee8;
  text-transform: uppercase;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.js-book-category {
  color: #b4afaf;
  text-transform: uppercase;
  letter-spacing: .42px;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.books-colortitle {
  letter-spacing: -.04em;
  text-align: left;
  color: #4f2ee8;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.books__image {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  display: block;
}

.books__overlay {
  background: #eac645;
  justify-content: center;
  align-items: center;
  width: calc(100% + 1px);
  height: 20%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.books__overlay-text {
  text-align: center;
  fill: #111;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
}

.books__wrapper:hover .books__overlay {
  transform: translateY(-99%);
}

.books__wrapper {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.books-title {
  letter-spacing: -.04em;
  color: #111;
  margin-block: 40px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.books-colortitle {
  letter-spacing: -1.92px;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
}

.books__info-title {
  line-break: auto;
  line-clamp: 180px;
  letter-spacing: -.04em;
  text-align: left;
  color: #111;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  overflow: hidden;
}

.books__info-author {
  letter-spacing: -.04em;
  text-align: left;
  color: #b4afaf;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.supporters {
  box-sizing: border-box;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  flex-direction: column;
  width: 100%;
  max-width: 358px;
  height: 358px;
  margin-top: 40px;
  padding: 24px 40px 20px;
  display: flex;
}

.supporters-title {
  gap: 12px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.supporters-title h3 {
  letter-spacing: -.04em;
  color: #fff;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
}

.supporters-button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 32px;
  min-height: 32px;
  margin: 0 auto;
  padding: 0;
  transition: transform .5s ease-in-out;
  display: flex;
  transform: rotateX(0);
}

.supporters__btn--up {
  transform: rotateX(180deg);
}

.swiper .supporters__item {
  align-items: center;
  gap: 16px;
  display: flex;
}

.swiper .supporters__img {
  object-fit: contain;
  object-position: left;
  filter: brightness(0) invert();
  aspect-ratio: 159 / 32;
  width: 100%;
  height: 32px;
}

.swiper .supporters__number {
  color: #fff;
  font-style: normal;
  font-weight: 400;
}

.see-more {
  border: 2px solid var(--accent-color);
  background-color: var(--main-bg-color);
  line-break: auto;
  letter-spacing: -.01em;
  color: var(--main-text-color-light-theme);
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 46px;
  margin-top: 8px;
  margin-left: auto;
  margin-right: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  overflow: hidden;
}

.see-more:hover, .see-more:focus {
  color: #fff;
  background-color: #4f2ee8;
}

.all-categories__btn {
  letter-spacing: -.01em;
  cursor: pointer;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  width: 200px;
  height: 50px;
  margin-top: 32px;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.all-categories__btn:hover {
  color: #fff;
  background-color: #4f2ee8;
}

.books-list {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.go-to-top {
  z-index: 8;
  background-color: #4f2ee8;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  transition: all .5s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 70px;
  right: 20px;
  box-shadow: 0 0 10px 3px #4f2ee84d;
}

.go-to-top:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 10px #4f2ee84d;
}

.svg-go-to-top {
  stroke: #fff;
  width: 24px;
  height: 24px;
}

@media screen and (width >= 768px) {
  .hero__grid {
    flex-direction: row;
    gap: 40px;
    display: flex;
  }

  .books-title {
    letter-spacing: -1.92px;
    margin-top: 100px;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 52px;
  }

  .category-list {
    height: 472px;
  }

  .books-list {
    grid-template-columns: repeat(3, 218px);
    gap: 40px 24px;
    display: grid;
  }

  .books__image {
    width: 218px;
    height: 316px;
  }

  .supporters {
    width: 100%;
    height: 100%;
    max-height: 472px;
    margin: 0;
  }

  .see-more {
    margin-top: 20px;
  }
}

@media screen and (width >= 1280px) {
  .books-title {
    letter-spacing: -1.92px;
    margin-top: 0;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 52px;
  }

  .container__home {
    grid-template-columns: 358px 1fr;
    column-gap: 40px;
    display: grid;
  }

  .books-container {
    margin: 0;
  }

  .books__image {
    width: 190px;
    height: 270px;
  }

  .hero__grid {
    flex-direction: column;
    gap: 40px;
    display: flex;
  }

  .category-list {
    height: 472px;
  }

  .books-list {
    grid-template-columns: repeat(4, 190px);
    display: grid;
  }

  .see-more {
    margin-top: 32px;
  }
}

@media screen and (width >= 1440px) {
  .books-list {
    grid-template-columns: repeat(5, 180px);
  }

  .books__image {
    width: 180px;
    height: 256px;
  }
}

.backdrop.is-active {
  visibility: visible;
  opacity: 1;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  visibility: hidden;
  z-index: 1000;
  opacity: 0;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal {
  background-color: #fff;
  border: 1px solid #111;
  border-radius: 20px;
  width: 336px;
  margin: 70px 15px 30px;
  padding: 40px 25px;
  position: relative;
}

.modal .store-img {
  width: 33px;
}

.book-store-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.close {
  color: #aaa;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  font-size: 28px;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.rendered-content {
  flex-direction: column;
  display: flex;
}

.book-cover {
  object-fit: contain;
  border-radius: 8px;
  align-items: stretch;
  width: 287px;
  margin-bottom: 14px;
}

.book-title {
  letter-spacing: -.04em;
  color: #111;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.book-author {
  letter-spacing: -.04em;
  color: #b4afaf;
  margin-bottom: 18px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.toggle-shopping-list {
  text-align: center;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: #111;
  cursor: pointer;
  border: 2px solid #4f2ee8;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 40px;
  padding: 14px 24px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.toggle-shopping-list:hover, .toggle-shopping-list:focus {
  background-color: #4f2ee8;
}

.toggle-shopping-list:disabled {
  opacity: .5;
  cursor: not-allowed;
  background-color: gray;
}

.modal .store-img {
  filter: grayscale();
}

.toggle-shopping-list_anonymous-user-content, .toggle-shopping-list_authenticated-user-content {
  text-align: center;
  margin-top: 8px;
  font-size: 12px;
  line-height: 14px;
}

@media screen and (width >= 768px) {
  .modal {
    width: 579px;
    padding: 40px;
  }

  .rendered-content {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .toggle-shopping-list__button-container {
    grid-column: span 2;
  }

  .book-cover {
    width: 192px;
    margin: 0;
  }

  .book-title {
    font-size: 24px;
    line-height: 28px;
  }

  .book-author {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 18px;
  }

  .toggle-shopping-list {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 18px;
    line-height: 24px;
  }
}

.header-bar {
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 64px;
  padding: 18px 20px;
  display: flex;
}

.button-shopping {
  gap: 4px;
  display: flex;
}

.button-shopping svg {
  fill: #111;
}

[data-page-name="home"] .button-home a, [data-page-name="shopping-list"] .button-shopping a {
  background-color: #eac645;
}

.btn-burger {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.btn-close {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
}

.close-icon {
  stroke: #111;
}

.burger-icon {
  stroke: #111;
  fill: #111;
}

.container-btn {
  justify-content: center;
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.container-logo {
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 28px;
  display: flex;
}

.switcher {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.theme-toggler {
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 200px;
  width: 42px;
  height: 24px;
  transition: all .2s;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

input {
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 200px;
  width: 42px;
  height: 24px;
  transition: all .2s;
  display: none;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-toggler:before {
  content: "";
  background-color: #fff;
  border-radius: 200px;
  width: 18px;
  height: 18px;
  margin: 3px;
  transition: all .2s;
  position: absolute;
}

input:checked + .theme-toggler {
  background-image: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .theme-toggler:before {
  transform: translateX(18px);
}

.menu {
  align-items: center;
  gap: 16px;
  display: none;
}

.button-container, .sign-up {
  display: none;
}

.menu li a, .mobile-modal-menu li a {
  color: #111;
  letter-spacing: -.56px;
  text-transform: uppercase;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
}

.sign-up.is-authenticated {
  letter-spacing: -.44px;
  justify-content: space-around;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.sign-up.is-authenticated img {
  width: 37px;
}

