  .header-bar {
    gap: 0;
    width: 100%;
    height: 72px;
  }

  .burger-icon {
    display: none;
  }

  .menu {
    justify-content: center;
    align-items: center;
    gap: 16px;
    display: none;
  }

  .menu.is-authenticated {
    display: inline-flex;
  }

  .button-home {
    background: none;
    border-radius: 36px;
    align-items: flex-start;
    gap: 10px;
    display: flex;
  }

  .home-p {
    color: #111;
    letter-spacing: -.56px;
    text-transform: uppercase;
    margin: 0;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
  }

  .button-shopping-list a {
    cursor: pointer;
    background-color: #0000;
    border: none;
    justify-content: center;
    align-items: center;
    gap: 4px;
    margin-right: 101px;
    display: flex;
  }

  .shopping-p {
    letter-spacing: -.56px;
    text-transform: uppercase;
    margin: 0;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
  }

  .log-out-wrapper {
    padding: 20px;
    position: relative;
  }

  .log-out-wrapper:before {
    content: "";
    opacity: 0;
    width: 184px;
    height: 48px;
    transition: opacity .3s ease-in-out;
    position: absolute;
    inset: -4px -31px -31px 110px;
  }

  .log-out-wrapper:hover:before {
    opacity: 1;
  }

  .button-log-out {
    opacity: 0;
    cursor: pointer;
    background: #fff;
    border: 1.5px solid #111;
    border-radius: 18px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    width: 184px;
    height: 48px;
    transition: transform .5s linear, opacity .5s linear;
    display: inline-flex;
    position: absolute;
    bottom: 0;
    left: 110px;
    transform: translateY(10%);
  }

  .log-out-wrapper:hover .button-log-out {
    opacity: 1;
    bottom: 0;
  }

  .sign-up {
    color: #fff;
    cursor: pointer;
    background: #4f2ee8;
    border: 1.5px solid #111;
    border-radius: 18px;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    gap: 90px;
    width: 184px;
    height: 48px;
    display: inline-flex;
  }

  .sign-up.is-authenticated {
    justify-content: flex-start;
    gap: 8px;
    padding-inline: 12px;
  }

  .sign-up-p {
    color: #fff;
    letter-spacing: -.56px;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
  }

  .sign-up-icon {
    margin: 0;
  }

  .button-container {
    display: unset;
    flex-shrink: 0;
    width: 184px;
    height: 48px;
  }

